<template>
  <div class="addNews">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="评议描述">
        <el-input v-model="form.title" style="width:80%;"></el-input>
      </el-form-item>
      <el-form-item label="主体内容">
        <editor  v-model="form.content"></editor>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="postForm" v-if="this.$route.name=='评议添加页'">添加</el-button>
        <el-button type="primary" @click="updateForm" v-if="this.$route.name=='评议编辑页'">保存并添加</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import editor from '../../components/editor'

  export default {
    name: "add",
    components: {
      editor
    },
    data() {
      return {
        form: {
          title: '',
          content: ''
        },
        arr: []
      }
    },
    methods: {
      getMsg() {
        let id = this.$route.query.id;
        let params = {id};
        this.$axios.get('getDiscuss', params).then(res => {
          this.form = res.data[0];
        })
      },
      postForm() {
        let params = this.form;
        this.$axios.post('addDiscuss', params).then(res => {
          if (res.code == 200) {
            this.$message({
              message: '添加成功',
              type: 'success'
            });
            this.$router.push('/discuss')
          } else {
            this.$message.error('添加失败');
          }
        });
      },
      updateForm() {
        let id = this.$route.query.id;
        let params = {
          ...this.form,
          id
        };
        this.$axios.post('updateDiscussOne', params).then(res => {
          if (res.code == 200) {
            this.$message({
              message: '修改成功',
              type: 'success'
            });
            this.$router.push('/discuss')
          } else {
            this.$message.error('修改失败');
          }
        });

      },
    },
    watch: {
      $route() {
        this.form = {
          title: '',
          contentText: '',
          content: ''
        }
      },
    },
    mounted() {
      if (this.$route.name == "评议编辑页") {
        this.getMsg();
      }

    }
  }
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }

  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }

  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }

  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }

</style>



